import React, { useState } from 'react';
import Image from 'next/image';
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer,
  PieChart, Pie, Cell,
} from 'recharts';

// --- MOCKED DATA (Based on the screenshot visual) ---
const currentMonthTotal = 23000000;
const percentageChangeVsLastMonth = 12;

const mockSignInData = [
    { day: 1, july: 12000000, june: 5000000 },
    { day: 5, july: 15000000, june: 7000000 },
    { day: 10, july: 18000000, june: 9000000 },
    { day: 15, july: 17000000, june: 8000000 },
    { day: 20, july: 21000000, june: 11000000 },
    { day: 25, july: 18000000, june: 10000000 },
    { day: 28, july: 23000000, june: 12000000 },
    { day: 30, july: 23000000, june: 17000000 }, 
];

const mockGenderData = [
    { name: 'Male', value: 60, color: '#4ade80' },
    { name: 'Female', value: 30, color: '#f87171' },
    { name: 'Others', value: 10, color: '#facc15' },
];

const mockMatchData = [
    { name: 'Still Looking', value: 30, color: '#22c55e' },
    { name: 'Successfully Matched', value: 45, color: '#3b82f6' },
    { name: 'Newly Registered', value: 20, color: '#f59e0b' },
    { name: 'Inactive', value: 5, color: '#9ca3af' },
];

const mockTopStats = [
    { title: "Revenue", value: "2.3 %", vs: "last week", trend: "up" },
    { title: "Orders", value: "12 %", vs: "last week", trend: "down" },
    { title: "Sign-ins", value: "18 %", vs: "last week", trend: "up" },
    { title: "Sign-ups", value: "5 %", vs: "last week", trend: "down" },
    { title: "Users", value: "10 %", vs: "last week", trend: "up" },
];

const initialMockData = {
    signInData: mockSignInData,
    genderData: mockGenderData,
    matchData: mockMatchData,
    topStats: mockTopStats,
    totalJulySignIns: currentMonthTotal,
    currentMonthName: 'July',
    previousMonthName: 'June',
};
// --- END MOCKED DATA ---


// --- Gradient Definitions for Pie Charts (for 3D look) ---
const GenderGradients = () => (
  <defs>
    <linearGradient id="genderMale" x1="0" y1="0" x2="1" y2="1"><stop offset="0%" stopColor="#4ade80" /><stop offset="100%" stopColor="#10b981" /></linearGradient>
    <linearGradient id="genderFemale" x1="0" y1="0" x2="1" y2="1"><stop offset="0%" stopColor="#f87171" /><stop offset="100%" stopColor="#ef4444" /></linearGradient>
    <linearGradient id="genderOthers" x1="0" y1="0" x2="1" y2="1"><stop offset="0%" stopColor="#facc15" /><stop offset="100%" stopColor="#f59e0b" /></linearGradient>
    <filter id="shadow" x="-20%" y="-20%" width="140%" height="140%"><feDropShadow dx="2" dy="2" stdDeviation="3" floodColor="#000" floodOpacity="0.15" /></filter>
  </defs>
);
const MatchGradients = () => (
  <defs>
    <linearGradient id="matchStillLooking" x1="0" y1="0" x2="0" y2="1"><stop offset="5%" stopColor="#22c55e" stopOpacity={0.9}/><stop offset="95%" stopColor="#16a34a" stopOpacity={1}/></linearGradient>
    <linearGradient id="matchSuccessfullyMatched" x1="0" y1="0" x2="0" y2="1"><stop offset="5%" stopColor="#3b82f6" stopOpacity={0.9}/><stop offset="95%" stopColor="#2563eb" stopOpacity={1}/></linearGradient>
    <linearGradient id="matchNewlyRegistered" x1="0" y1="0" x2="0" y2="1"><stop offset="5%" stopColor="#f97316" stopOpacity={0.9}/><stop offset="95%" stopColor="#ea580c" stopOpacity={1}/></linearGradient>
    <linearGradient id="matchInactive" x1="0" y1="0" x2="0" y2="1"><stop offset="5%" stopColor="#9ca3af" stopOpacity={0.9}/><stop offset="95%" stopColor="#6b7280" stopOpacity={1}/></linearGradient>
    <filter id="shadow" x="-20%" y="-20%" width="140%" height="140%"><feDropShadow dx="2" dy="2" stdDeviation="3" floodColor="#000" floodOpacity="0.15" /></filter>
  </defs>
);


// --- Custom Tooltip for Line Chart (Matches screenshot style) ---
const CustomLineChartTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
        let point1 = null;
        let point2 = null;
        
        // Exact values and dates as shown in the screenshot for day 30
        if (label === 30) {
             point1 = { date: "July 07, 2025 | 5:02", value: Number(23000000).toLocaleString('en-US', { minimumFractionDigits: 2 }) };
             point2 = { date: "June 08, 2024 | 6:02", value: Number(17000000).toLocaleString('en-US', { minimumFractionDigits: 2 }) };
        } else {
             // Generic tooltip for other points, ensuring consistent formatting
             const julyData = payload.find(p => p.dataKey === 'july');
             const juneData = payload.find(p => p.dataKey === 'june');

             if (julyData) {
                point1 = { date: `July ${label}, 2025`, value: julyData.value.toLocaleString('en-US', { minimumFractionDigits: 2 }) };
             }
             if (juneData) {
                point2 = { date: `June ${label}, 2024`, value: juneData.value.toLocaleString('en-US', { minimumFractionDigits: 2 }) };
             }
        }

        return (
          <div className="bg-black/80 backdrop-blur-sm shadow-xl p-3 rounded-lg text-white text-sm border border-gray-700 space-y-1">
            {point1 && (
                <div className="text-right">
                    <p className="font-semibold text-xs text-gray-400">{point1.date}</p>
                    <p className="text-white text-base">{point1.value}</p>
                </div>
            )}
            {point2 && (
                <div className="text-right border-t border-gray-700/50 pt-1 mt-1">
                    <p className="font-semibold text-xs text-gray-400">{point2.date}</p>
                    <p className="text-white text-base">{point2.value}</p>
                </div>
            )}
          </div>
        );
    }
    return null;
};

// --- Custom Label component for PieChart (Matches screenshot visuals) ---
const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, name, value, index, payload }) => {
    const x = cx + (outerRadius * 1.3) * Math.cos(-midAngle * Math.PI / 180);
    const y = cy + (outerRadius * 1.3) * Math.sin(-midAngle * Math.PI / 180);
    const textAnchor = x > cx ? 'start' : 'end';
    
    const lineX1 = cx + outerRadius * Math.cos(-midAngle * Math.PI / 180);
    const lineY1 = cy + outerRadius * Math.sin(-midAngle * Math.PI / 180);

    const lineX2 = cx + (outerRadius + 8) * Math.cos(-midAngle * Math.PI / 180);
    const lineY2 = cy + (outerRadius + 8) * Math.sin(-midAngle * Math.PI / 180);
    
    const percentageText = `${(percent * 100).toFixed(0)}%`;

    let labelLines = [name, percentageText];
    if (name === 'Successfully Matched') {
        // Splitting the name to match the screenshot's vertical text
        labelLines = [`Successfully`, `Matched`, percentageText];
    } else if (name === 'Newly Registered') {
        labelLines = [`Newly`, `Registered`, percentageText];
    }
    
    // Filter out very small slices, except 'Inactive'
    if (percent * 100 < 5 && name !== 'Inactive') return null; 

    // Determine the vertical starting position for the label text block
    let startY = y;
    if (name === 'Male' || name === 'Female' || name === 'Still Looking' || name === 'Inactive') {
        // Adjust vertically for single-line labels to align better with the elbow
        startY = y + (labelLines.length === 2 ? -6 : 0);
    } else {
         // Adjust vertically for multi-line labels (Successful/Newly Registered)
        startY = y - 12; 
    }

    return (
        <g>
            {/* Connector Line (Elbowed line) */}
            <polyline 
                points={`${lineX1},${lineY1} ${lineX2},${lineY2} ${x},${startY + 12}`} 
                stroke={payload.color || '#333'} 
                fill="none" 
                strokeWidth={1}
                strokeDasharray="2 2"
            />

            {/* Render the label lines (Name and Percentage) */}
            {labelLines.map((line, i) => (
                <text 
                    key={i} 
                    x={x} 
                    y={startY + (i * 12)} 
                    fill="#000" 
                    textAnchor={textAnchor} 
                    dominantBaseline="central" 
                    style={{
                        fontWeight: line.includes('%') ? 500 : 600, 
                        fontSize: line.includes('%') ? '10px' : '11px',
                        fill: line.includes('%') ? '#555' : '#000'
                    }}
                >
                    {line}
                </text>
            ))}
        </g>
    );
};


// --- StatsSplitCard Component (for top row) ---
const StatsSplitCard = ({ title, value, vs, trend }) => {
    const isUp = trend === 'up';
    const textColor = isUp ? 'text-green-600' : 'text-red-600';
    const arrow = isUp ? '▲' : '▼';
    const arrowColor = isUp ? 'bg-green-100' : 'bg-red-100';

    // Conditional styling based on screenshot layout (some items only have a single word label on top)
    const isSingleLabel = title === 'Revenue' || title === 'Users';

    return (
        <div className={`flex-1 min-w-[150px] bg-white rounded-xl shadow-lg border border-gray-200 p-4 ${isSingleLabel ? 'flex flex-col items-center justify-center text-center' : 'flex items-center justify-between gap-4'}`}>
            <div className='flex items-center gap-2'>
                <div className={`w-8 h-8 rounded-full flex items-center justify-center ${arrowColor} ${textColor} text-base font-bold`}>
                    {arrow}
                </div>
                <div>
                    <h4 className="text-base font-bold text-gray-800">{title}</h4>
                    <span className="text-sm text-gray-500">{value}</span>
                </div>
            </div>
            
            <span className="text-sm text-gray-500 font-medium whitespace-nowrap">{vs}</span>
        </div>
    );
};


// --- DashboardFilters Component (for bottom search/filter bar) ---
const DashboardFilters = () => {
    return (
        <div className="w-full bg-white rounded-xl shadow-lg border border-gray-200 p-4 flex flex-col md:flex-row items-center gap-4">
            <div className="relative flex-1 w-full md:w-auto">
                <input
                    type="text"
                    placeholder="Search By User ID"
                    className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                />
                <svg className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
            </div>
            <div className="flex flex-col sm:flex-row gap-4 w-full md:w-auto">
                <select className="border border-gray-300 rounded-md px-4 py-2 text-sm text-gray-700 bg-white shadow-sm focus:ring-blue-500 focus:border-blue-500 w-full md:w-auto">
                    <option>Status</option>
                    <option>Active</option>
                    <option>Inactive</option>
                </select>
                <select className="border border-gray-300 rounded-md px-4 py-2 text-sm text-gray-700 bg-white shadow-sm focus:ring-blue-500 focus:border-blue-500 w-full md:w-auto">
                    <option>Gender</option>
                    <option>Male</option>
                    <option>Female</option>
                    <option>Others</option>
                </select>
                <button className="flex items-center justify-center gap-2 px-4 py-2 bg-black text-white text-sm font-medium rounded-md shadow-sm hover:bg-gray-800 focus:outline-none focus:ring-2 focus:ring-black focus:ring-offset-2 w-full md:w-auto">
                    <svg className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 10v6m0 0l-3-3m3 3l3-3m2 8H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                    </svg>
                    Export CSV
                </button>
            </div>
        </div>
    );
};


// --- Main Analytics Page Component ---
export default function AnalyticsPage() {
  const [analyticsData] = useState(initialMockData);
  const diffTextClass = percentageChangeVsLastMonth >= 0 ? 'text-green-600' : 'text-red-600';
  const diffSign = percentageChangeVsLastMonth >= 0 ? '▲' : '▼'; 

  return (
    <div className="flex flex-col gap-8 p-5 md:p-8 bg-gray-50 min-h-screen">
      
      {/* 1. Top Analytics Cards */}
      <div className="flex flex-wrap gap-4 justify-between">
        {analyticsData.topStats.map((stat, index) => (
          // Adjusted to match the exact number of cards (5) in the screenshot row
          <StatsSplitCard key={index} {...stat} />
        ))}
      </div>

      {/* 2. Main Chart Area */}
      <div className="flex flex-col lg:flex-row flex-wrap gap-8">
        
        {/* 2a. Sign-In Analytics Card (Line Chart) */}
        <div className="w-full lg:w-[60%] bg-white rounded-xl shadow-lg border border-gray-200 p-6 md:p-8">
          <div className="flex flex-col sm:flex-row justify-between items-start mb-6 gap-4">
            <div>
              <h2 className="text-xl md:text-2xl font-bold text-gray-800 mb-2">Sign-In Analytics</h2>
              
              <div className='flex items-center gap-3'>
                  <p className="text-3xl md:text-4xl font-extrabold text-gray-900">
                      {analyticsData.totalJulySignIns.toLocaleString('en-US')}. {/* Hydration Fix */}
                  </p>
                  <div className="flex flex-col items-start">
                      <p className={`${diffTextClass} text-xl font-bold flex items-center`}>
                          {diffSign}
                          {`${percentageChangeVsLastMonth}%`}
                      </p>
                      <span className="text-gray-500 text-sm">vs last Month</span>
                  </div>
              </div>
            </div>
            <div className="flex items-center flex-wrap gap-3">
              <span className="flex items-center gap-1 text-sm font-medium text-black"><span className="w-2 h-2 rounded-full bg-black"></span> {analyticsData.currentMonthName}</span>
              <span className="flex items-center gap-1 text-sm text-gray-500"><span className="w-2 h-2 rounded-full bg-gray-400"></span> {analyticsData.previousMonthName}</span>
              <select className="border border-gray-300 rounded-md px-3 py-1 text-sm text-gray-700 bg-white shadow-sm focus:ring-blue-500 focus:border-blue-500">
                <option>Month</option>
              </select>
            </div>
          </div>

          <div className="w-full h-[320px] sm:h-[380px] md:h-[450px]">
            <ResponsiveContainer width="100%" height="100%">
              <LineChart data={analyticsData.signInData}>
                <CartesianGrid strokeDasharray="4 4" stroke="#e0e0e0" vertical={false} />
                <XAxis dataKey="day" tickLine={false} axisLine={{ stroke: '#cccccc' }} style={{ fontSize: '12px', fill: '#555' }} padding={{ left: 10, right: 10 }} />
                <YAxis 
                  domain={[0, 45000000]} 
                  tickLine={false} 
                  axisLine={{ stroke: '#cccccc' }} 
                  tickFormatter={(value) => `${value / 1000000} M`} 
                  style={{ fontSize: '12px', fill: '#555' }} 
                  ticks={[0, 5000000, 10000000, 15000000, 20000000, 25000000, 30000000, 35000000, 40000000, 45000000]}
                />
                <Tooltip content={<CustomLineChartTooltip />} cursor={{ stroke: '#555', strokeWidth: 1, strokeDasharray: '3 3' }} />
                <Line type="monotone" dataKey="july" stroke="#000" strokeWidth={2} dot={false} activeDot={{ r: 5, strokeWidth: 3, fill: '#000' }} />
                <Line type="monotone" dataKey="june" stroke="#999" strokeDasharray="4 4" strokeWidth={2} dot={false} activeDot={{ r: 5, strokeWidth: 3, fill: '#999' }} />
              </LineChart>
            </ResponsiveContainer>
          </div>
        </div>
        
        {/* 2b. Right Column (Gender & Matchmaking) */}
        <div className="w-full lg:w-[36%] flex flex-col gap-8">
          
          {/* Gender Analytics Card */}
          <div className="bg-white rounded-xl shadow-lg border border-gray-200 p-6">
            <div className="flex justify-between items-center mb-4 flex-wrap gap-2">
              <h3 className="font-bold text-lg text-gray-800">Gender Analytics</h3>
              <div className="flex gap-2 text-xs font-medium flex-wrap">
                  <span className="flex items-center gap-1 text-green-700"><div className="w-3 h-3 bg-green-500 rounded-full border border-gray-200 shadow-sm"></div> Male</span>
                  <span className="flex items-center gap-1 text-red-700"><div className="w-3 h-3 bg-red-500 rounded-full border border-gray-200 shadow-sm"></div> Female</span>
                  <span className="flex items-center gap-1 text-yellow-700"><div className="w-3 h-3 bg-yellow-500 rounded-full border border-gray-200 shadow-sm"></div> Ots.</span>
              </div>
            </div>
            
            <div className="w-full h-[250px] relative">
              <ResponsiveContainer width="100%" height="100%">
                <PieChart>
                  <GenderGradients /> 
                  <Pie
                    data={analyticsData.genderData}
                    cx="50%"
                    cy="50%"
                    innerRadius={0}
                    outerRadius={95}
                    paddingAngle={0}
                    dataKey="value"
                    labelLine={true} 
                    label={renderCustomizedLabel} 
                  >
                    {analyticsData.genderData.map((entry, index) => (
                      <Cell
                        key={`gender-cell-${index}`}
                        fill={`url(#gender${entry.name})`} 
                        filter="url(#shadow)"
                        stroke="#fff"
                        strokeWidth={1}
                      />
                    ))}
                  </Pie>
                </PieChart>
              </ResponsiveContainer>
            </div>
          </div>

          {/* Matchmaking Status Card */}
          <div className="bg-white rounded-xl shadow-lg border border-gray-200 p-6">
            <div className="flex justify-between items-center mb-4 flex-wrap gap-2">
              <h3 className="font-bold text-lg text-gray-800">Matchmaking Status</h3>
              <div className="flex flex-wrap gap-3 text-xs font-medium">
                  <span className="flex items-center gap-1 text-green-700"><div className="w-3 h-3 bg-green-500 rounded-full shadow-sm"></div> Still Looking</span>
                  <span className="flex items-center gap-1 text-blue-700"><div className="w-3 h-3 bg-blue-500 rounded-full shadow-sm"></div> Successful...</span>
                  <span className="flex items-center gap-1 text-orange-700"><div className="w-3 h-3 bg-orange-500 rounded-full shadow-sm"></div> Newly Reg.</span>
                  <span className="flex items-center gap-1 text-gray-700"><div className="w-3 h-3 bg-gray-500 rounded-full shadow-sm"></div> Inactive</span>
              </div>
            </div>
            
            <div className="w-full h-[250px] relative">
              <ResponsiveContainer width="100%" height="100%">
                <PieChart>
                  <MatchGradients /> 
                  <Pie
                    data={analyticsData.matchData}
                    cx="50%"
                    cy="50%"
                    innerRadius={65}
                    outerRadius={95}
                    paddingAngle={4}
                    dataKey="value"
                    labelLine={true}
                    label={renderCustomizedLabel}
                  >
                    {analyticsData.matchData.map((entry, index) => (
                      <Cell
                        key={`match-cell-${index}`}
                        fill={`url(#match${entry.name.replace(/\s+/g, '')})`} 
                        filter="url(#shadow)"
                        stroke="#fff"
                        strokeWidth={1}
                      />
                    ))}
                  </Pie>
                </PieChart>
              </ResponsiveContainer>
            </div>
            
            <p className="text-center text-sm text-gray-600 mt-3 italic font-medium">
              {`"Out Of 20M , 5M is Matched"`}
            </p>
          </div>
        </div>
      </div> {/* End of Main Content Area */}

      {/* 3. Bottom Filter/Search Section */}
      <DashboardFilters />
    </div>
  );
}